<template>
	<div>
		<Breadcrumb class="bread">
			<div class="title">管理期权奖励</div>
			<div class="b_func">
				<div class="searchForm">
					 <Select v-model="thisPlan" style="width:200px" placeholder="请选择期权计划" size="large" @on-change="goPlanList">
				        <Option v-for="(item,index) in planList" :value="item.id" :key="index">{{ item.label }}</Option>
				    </Select>
				</div>
				<div class="addPlan">
					<Button type="primary" :disabled="disabled" size="large" icon="md-barcode" @click="addUser">添加激励对象</Button>
				</div>
			</div>
		</Breadcrumb>
		<div>
			<Row>
				<Col span="18">
					<Card :bordered="false" class="capTable">
						<p slot="title" class="tipMessage">期权奖励列表<Icon type="ios-help-circle-outline" style="position: relative;top: -1px;cursor: pointer;" color="#2d8cf0" size="22" @click="helpBox = !helpBox"/></p>
						<div class="tabBoard">
							<Table height="700" :loading="loading" :border="true" :stripe="true" :columns="planTabList.rows" :data="planTabList.cols"></Table>
						</div>
					</Card>
				</Col>
				<Col span="6">
					<Card :bordered="false" class="tokenBoard">
						<p slot="title">期权数据详情</p>
						<div class="msgBoard">
							<p v-for="item in msgList">
								<b>{{item.title}}</b>
								{{item.vals}}
							</p>
						</div>
					</Card>
				</Col>
			</Row>
		</div>
		 <Modal
			v-model="helpBox"
			title="期权机制描述"
			class-name="vertical-center-modal">
			<p>本公司计划于<b>【创建日期】</b>创建名为<b>【机制名称】</b>的期权激励计划（ESOP），该ESOP将预留共计<b>【期权授予量】</b>QT，约占公司股权的 * 作为期权池。</p>
			<p>该期权激励机制下，员工将会在第一个到期日被授予<b>【首次授权量】</b>的股份，其余的股份须于首个到期日期起按<b>【时间单位】</b> 授予，总计<b>【周期】</b> 个【时间单位】。</p>
			
			<div slot="footer" class="tipMsg">
				<p>期权发行条款:</p>
				<p>期权严格按照既定计划发放。</p>
				<p>如果员工不再受雇于公司,任何未成熟期权将自动失效，并放回公司期权池。</p>
			</div>
		</Modal>
	</div>
</template>

<script>
	import Qs from 'qs'
	import {mapState} from 'vuex'
	import mutil from '@/util/mutil'
	export default {
		data(){
			return{
				helpBox:false,
				thisPlan:'',
				loading:false,
				planList:[],
				disabled:true,
				planTabList:{
					rows:[
						{
							title: '激励对象',
							key: 'name',
							tooltip:true,
							align:'center'
						},
						{
							title: '期权授予量',
							key: 'allNum',
							tooltip:true,
							align:'center'
						},
						{
							title: '成熟期权',
							key: 'getNum',
							tooltip:true,
							align:'center'
						},
						{
							title: '已行权期权',
							key: 'endNum',
							tooltip:true,
							align:'center'
						},
						{
							title: '等待期权',
							key: 'startNum',
							tooltip:true,
							align:'center'
						},
						{
							title: '到期日',
							key: 'overDate',
							tooltip:true,
							align:'center'
						}
					],
					cols:[]
				},
				msgList:[
					{
						title:'机制名称',
						vals:''
					},
					{
						title:'授权周期',
						vals:''
					},
					{
						title:'创建日期',
						vals:''
					},
					{
						title:'期权池总量',
						vals:''
					},
					{
						title:'期权池剩余',
						vals:''
					}
				]
			}
		},
		computed: {
			...mapState({
				Address: state => state.web3.coinbase
			})
		},	
		methods:{
			addUser(){
				this.$router.push({
					path:'createUser',
					query:{
						"only":this.$route.query.only,
						"planId":this.thisPlan
					}
				})
			},
			goPlanList(val){
				this.disabled = false
				let data = {
					"only": this.$route.query.only,
					"option":val
				};
				this.$axios({
					method: 'post',
					url: '/index.php/cn/home/node_se/excitation',
					data: Qs.stringify(data)
				}).then((response) => {
					if(response.data.state == 0){
						let planListDetail = response.data.info
						let planMsg = response.data.optioninfo
						let arr = []
						for(let i=0;i<planListDetail.length;i++){
							arr.push({
								name: planListDetail[i].surname + planListDetail[i].name ,
								allNum:planListDetail[i].token_number,
								getNum:planListDetail[i].state_a,
								endNum:planListDetail[i].state_b,
								startNum:planListDetail[i].state_c,
								overDate:this.returnTypeDate(planListDetail[i].expire_time)
							})
						}
						this.planTabList.cols = arr
						this.msgList[0].vals = planMsg.name
						this.msgList[1].vals = planMsg.total_month + this.returnTypeDate(planMsg.grant_type)
						this.msgList[2].vals = mutil.timestampToTime(planMsg.option_time)
						this.msgList[3].vals = planMsg.token_number
						this.msgList[4].vals = planMsg.token_number - planMsg.token_grant
					}
				})
			},
			refreshPlanList(){
				let data = {
					"only": this.$route.query.only
				};
				this.$axios({
					method: 'post',
					url: '/index.php/cn/home/node_se/option',
					data: Qs.stringify(data)
				}).then((response) => {
					if(response.data.state == 0){
						let planListDetail = response.data.info
						this.planList = []
						if(planListDetail.length > 0){
							for(let i=0;i<planListDetail.length;i++){
								this.planList.push({
									id:planListDetail[i].id,
									label:planListDetail[i].name
								})
							}
						}
					}
				})
			},
			returnTypeDate(msg){
				switch(msg){
					case '1':
						msg = '月'
						break;
					case '2':
						msg = '季'
						break;
					case '3':
						msg = '半年'
						break;
					case '4':
						msg = '年'
						break;
				}
				return msg
			}
		},
		created(){
			this.$emit('menuActiveName', "14-2")
			this.$emit('menuOpenNames', "14")
			this.refreshPlanList()
		}
	}
</script>
<style>
	.warningProgress .ivu-progress-bg{
		background-color: #ed4014
	}
	.tooltip-msg{
		width: 300px	
	}
	.ivu-tooltip{width: 100%;}
</style>
<style scoped lang="stylus">
	flexs()
		display:flex
		align-items: center
		justify-content: space-between
		
	.bread
		flexs()
		.b_func
			flexs()
			flex-basis: 400px
			.searchForm
				flexs()
				.radio
					margin-right: 30px
				button:hover
					color: #2d8cf0
					
	.capTable
		min-height: 300px
		background: white
		margin-right: 30px;
		.tabBoard
			.ivu-table-wrapper
				border-top: 0;
				.ivu-table:after
					background-color: transparent
				
	
	.tokenBoard,.userBoard
		.msgBoard
			padding: 14px 16px;
			p
				margin-top: 14px
				span
					display:block;
					width:100%;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
				b
					margin-right: 16px
				&:first-child
					margin-top: 0;
	
	.tipMsg
		text-align: left;
</style>




